import {notification} from 'antd';
import IResponse from "./l-utils/request/IResponse";
import history from './utils/history';
import PageInfo from "./utils/pageHelper/PageInfo";

const PROJECT_NAME = process.env.REACT_APP_PROJECT_NAME || "";
const notice = notification;
/**
 *  
 */
const config = {

    notice,

    // 
    request: {
        prefix: 'http://218.85.80.181:12302',
        /**
         * modelEnhance
         * 
         * {status: true, data: ...} // 
         * {status: false, message: ...} // 
         * 
         * response
         * 
         */
        afterResponse: (response:IResponse) => {
            const { status, message } = response;
            if (status) {
                return response;
            } else {
                // session6 session
                if (Math.floor(response.code/100) === 6) {
                    history.push(PROJECT_NAME+'/sign/login');
                }else if (response.code === 400) {
                    // history.push(PROJECT_NAME+'/sign/login');
                    notice.error({
                        message: '',
                        description:message
                    });
                }else {
                    notice.error({
                        message: '',
                        description:message
                    });
                }
                return response;
            }
        },
        errorHandle: (err: any) => {
            // 
            if (err.name === 'RequestError') {
                // notice.error(err.text || err.message);
            }
        }
    },

    // 
    exception: {
        global: (err:any) => {
            const errName = err.name;
            // RequestError
            if (errName === 'RequestError') {
                console.error(err);
            } else {
                console.error(err);
            }
            notice.error(err.message);
        }
    },

    // 
    pageHelper: {
        // 
        requestFormat: (pageInfo:PageInfo) => {
            const { pageNum, pageSize, filters, sorts } = pageInfo;
            return {
                page: pageNum,
                size: pageSize,
                sortMap: sorts,
                queryCondition: filters
            };
        },

        // 
        responseFormat: (respData:any) => {
            const {
                pageNum,
                pageSize,
                total,
                list,
                pages
            } = respData;
            return {
                pageNum,
                pageSize,
                total,
                totalPages: pages,
                list
            };
        }
    },

    // 
    /*router: {
        loading: <PageLoading loading />
    },*/

    /**
     * 
     * 
     * 
     * {
     *   status: true,
     *   data: responseData
     * }
     * 
     * {
     *   status: false,
     *   code: 500,
     *   message: ''
     * }
     *  orz...
     */
    mock: {
        toSuccess: (response:any) => ({
            status: true,
            data: response
        }),

        toError: (message:string) => ({
            status: false,
            message
        })
    },
    // 
    PROJECT_NAME
};
export default config;


