const codeMessage = {
    400: ',',
    401: '',
    403: '',
    404: '',
    405: '',
    406: '',
    410: '',
    422: '',
    500: '',
    502: '',
    503: '',
    504: '',
    600: 'session ',
    601: 'session ',
    0: '',
    invalidURL: 'URL',
    requestCanceled: '',
};

export default class RequestError extends Error {
  public name = 'RequestError';
  public text: string;
  public code: number|string;
  public date: Date;
  public response:any;

  constructor(message:string, code:string|number = 0) {
    super(message);

    this.text = codeMessage[code];
    this.code = code;
    this.date = new Date();
  }
}


